import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiBadge from "@/components/badge/badge"
import tuiNomore from "@/components/nomore/nomore"
import tuiButton from "@/components/button/button"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
import tuiBottomPopup from "@/components/bottom-popup/bottom-popup"
import tuiNumberbox from "@/components/numberbox/numberbox"
import tuiModal from "@/components/modal/modal"

export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiBadge,
		tuiNomore,
		tuiButton,
		tuiTopDropdown,
		tuiBottomPopup,
		tuiNumberbox,
		tuiModal,
	},
	data() {
		return {
			id:'',
			goods:{},
			click:{},
			favorite: false,
			modalToken: false,
			modelBtnText:'一键复制',
			
			modalAuthorize: false,
			video:false,
			
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,
			bannerIndex: 0,
			topMenu: [{
				icon: "message",
				text: "消息",
				size: 26,
				badge: 3
			}, {
				icon: "home",
				text: "首页",
				size: 23,
				badge: 0
			}, {
				icon: "people",
				text: "我的",
				size: 26,
				badge: 0
			}, {
				icon: "cart",
				text: "购物车",
				size: 23,
				badge: 2
			}, {
				icon: "kefu",
				text: "客服小蜜",
				size: 26,
				badge: 0
			}, {
				icon: "feedback",
				text: "我要反馈",
				size: 23,
				badge: 0
			}, {
				icon: "share",
				text: "分享",
				size: 26,
				badge: 0
			}],
			menuShow: false,
			popupShow: false,
			value: 1,
			danmuList: [{
					text: '',
					color: '#ff0000',
					time: 3
				}
			],
		}
	},
	onLoad: function(options) {
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();








		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.scrollH = res.windowWidth
			}
		})
		//接收传值,id里面放的是标题，因为测试数据并没写id
		let id = options.id;
		// console.log('detail:'+id)
		if(options.video){
			this.video = true;
		}
		if(id){
			this.id = id;
			this.query_detail(id);
			this.pin(id);
		}
	},
	methods: {
		goHome: function() {
			uni.navigateTo({
				url:'/pages/index/index'
			})
		},
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				current: this.goods.goods.images[index],
				urls: this.goods.goods.images
			})
		},
		back: function() {
			uni.navigateBack()
		},
		openMenu: function() {
			this.menuShow = true
		},
		closeMenu: function() {
			this.menuShow = false
		},
		showPopup: function() {
			this.popupShow = true
		},
		hidePopup: function() {
			this.popupShow = false
		},
		change: function(e) {
			this.value = e.value
		},
		common: function() {
			this.tui.toast("功能开发中~")
		},
		//收藏
		toFavorite(){
			this.$http.post('/cms/member/favorite/pin', {object_type:'goods',object_id:this.id}).then(res => {
				if(res.info.status==0){
					this.favorite = true;
				}else{
					this.favorite = false;
				}
			}).catch(err => {});
		},
		buy(){
			if(!this.click.click_url){
				this.query_click(this.id,1);
			}else{
				if(this.click.authorize_url){
					this.modalAuthorize = true;
				}else{						
					window.location.href = this.click.click_url;
				}
			}
		},
		showToken(){
			if(!this.click.click_url){
				this.query_click(this.id,2);
			}else{
				this.modalToken = true;
			}
		},
		query_detail(id){
			uni.showLoading({
				title: '请稍候...'
			})
			this.$http.post('/cms/goods/view', {num_iid:id}).then(res => {
				if(res.data.item&&res.data.item){
					this.goods = res.data.item;
				}
				uni.hideLoading();
			}).catch(err => {
				uni.hideLoading();
			});
			this.$http.post('/cms/member/favorite/index', {object_type:'goods',object_id:id}).then(res => {
				if(res.data.item&&res.data.item.id){
					this.favorite = true;
				}else{
					this.favorite = false;
				}
			}).catch(err => {});
		},
		query_click(id,goIndex){
			this.$http.post('/cms/goods/click', {num_iid:id}).then(res => {
				if(res.data.click&&res.data.click){
					this.click = res.data.click;
					if(goIndex==1){
						this.buy();
					}
					else if(goIndex==2){
						this.showToken();
					}
				}
			}).catch(err => {});
		},
		pin(id){
			this.$http.post('/cms/member/history/pin', {object_type:'goods',object_id:id}).then(res => {
				
			}).catch(err => {});
		},
		hideToken() {
			this.modalToken= false;
		},
		hideAuthorize(){
			this.modalAuthorize= false;
		},
		copyContent(){
			uni.setClipboardData({ 
			data:'复制框内整段文字，打开【手-机-淘-宝】即可领券购买。'+this.click.tao_token, 
			success: (r=>{
				this.modelBtnText = '复制成功';
				uni.showToast({
					title: '复制成功',
					duration: 2000
				});
			}),
			fail:(r=>{
				this.modelBtnText = '复制失败，请手工复制';
				uni.showToast({
				    title: '复制失败，请手工复制',
				    duration: 2000
				});
			}), complete:(r=>{
				this.modelBtnText = '复制完成';
			})})
		},
		toAuthorize(){
			window.location.href = this.click.authorize_url;
		},
		tobuy(){
			this.modalAuthorize = false;
			window.location.href = this.click.click_url;
		}
	},
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	}
}
